import request from '@/utils/request';

// 
export function getList(pageIndex) {
	return request({
		url: '/getList',
		method: 'post',
		data: {
			pageIndex: pageIndex
		},
		headers: {
			showLoading: true, //loading
			showLoadingType: 'progress', //:loading(loading)  progress()
		}
	})
}
